
enum Specifications {
    RAM = 'Оперативная память',
    CPUSeries = 'Серия процессора.',
    TypeOfRAM = 'Тип оперативной памяти',
    NumberOfCPUCores = 'Количество ядер процессора',
    DisplayDiagonal = 'Диагональ дисплея',
    ScreenMatrixType = 'Тип матрицы экрана',
    Webcam = 'Веб камера',
    CPUFrequency = 'Частота процессора',
    DisplayResolution = 'Разрешение дисплея',
    CPU = 'Процессор',
    TypeOfDataCarrier = 'Тип носителя данных',
    TypeOfGPU = 'Тип видеокарты',
    SSDSolidStateDriveCapacity = 'Объем твердотельного накопителя SSD',
    CPUFrequencyTurbo = 'Частота процессора (режим Turbo)',
    SoundSystem = '',
    ANTUTU9 = 'Antutu 9 Результат',
    BATTERY = 'Аккумулятор',
    DISPLAY = "Разрешение",
    DPI = "Точек на дюйм",
    GEEKBENCH_MC = "GeekBench 5 Multi-Core",
    GEEKBENCH_SC = "GeekBench 5 Single Core",
    GPU = "Графический процессор",
    INTERNAL_STORAGE = "Внутренняя память",
    SP = "Защита экрана",
    TECHNOLOGIES = "Технологии"

}

export default function(){
    
    return function(keys: string[], compareData: any, product: any){
        const totalData: {[propName: string]: number} = {}
        console.log(keys, product, compareData)
        keys.forEach((key: string) => {

            let compareDataKeys = Object.keys(compareData)
            let spec = product.property[key]
                
            compareDataKeys.forEach((compareKey: string) => {
                if(Specifications[compareKey as keyof typeof Specifications] === key){
                    console.log(compareData[compareKey])
                    let compareValue = compareData[compareKey][spec]
                    if(compareValue !== undefined){
                        console.log(1)
                        totalData[key] = compareValue
                    }
                }
            })
        })

        return totalData    
    }
}